.navbar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 0 15px 0 rgba(34, 60, 80, 0.1);
    max-height: 1300px;
    a {
        text-decoration: none;
        color: gray;
        font-weight: 600;
        &:hover {
            color: #2254f5
        }
        &.active {
            color: burlywood;
        }
    }
    .navItem {
        font-size: 18px;
        margin-bottom: 30px;
    }


}

